export {
  getLocationAPI,
  getYelpResult,
  getYelpReview,
  getYelpBusiness
};

function getLocationAPI() {
  return fetch('https://location.services.mozilla.com/v1/geolocate?key=test')
    .then(res => res.json());
}

function getYelpResult(categories, params, offset) {
  if(offset === undefined) {
    offset = 0;
  }
  let search = {
    categories: categories,
    latitude: params.lat,
    longitude: params.lon,
    radius: params.radius,
    term: params.vegan,
    cost: params.cost,
    offset: offset
  };
  return fetch(`${URL}`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    // Make into object then stringify; JSON error @ position 0 is because of non-JSON data being sent
    body: JSON.stringify(search)
  })
    .then(res => {
      console.log('we got sometihng back???', res);
      if(res.error) {
        return null;
      }
      console.log('result is', res);
      return res.json().then(data => {
        return data;
      });
    });
}

function getYelpReview(id) {
  id = { id: id };
  return fetch(`${URL}/review`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(id)
  })
    .then(res => {
      return res.json().then(data => data);
    });
}

function getYelpBusiness(id) {
  id = { id: id };
  return fetch(`${URL}/business`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(id)
  })
    .then(res => {
      return res.json().then(data => data);
    });
}